<template>
  <v-layout row>
    <v-dialog v-model="dialog" persistent max-width="500">
      <template v-slot:activator="{ on }">
        <v-btn color="success" dark v-on="on">Get playlist kewords</v-btn>
      </template>
      <v-card>
        <v-card-title class="headline">Get playlist kewords?</v-card-title>
        <v-alert :value="showError" dismissible type="error">{{errorMessage}}</v-alert>
        <v-alert :value="showSuccess" dismissible type="success">{{successMessage}}</v-alert>
        <v-card-text v-show="loading">
          We're currently creating the job for extracting the keywords
          <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
        </v-card-text>
        <v-card-text>You're about to start a process to get this playlist keywords, this process can have several time depending on the data amount or the already queued processes.</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="success" flat @click="getPlaylistKeyWords">Start process</v-btn>
          <v-btn color="error" flat @click="dialog = false">Cancel, I will do it in other time</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
import _get from "lodash.get";
import { setTimeout } from "timers";
export default {
  data() {
    return {
      dialog: false,
      showError: false,
      errorMessage: "",
      showSuccess: false,
      successMessage: "",
      loading: false
    };
  },
  methods: {
    async getPlaylistKeyWords() {
      this.loading = true;
      console.log("this.$route.params", this.$route.params);
      const playlistId = _get(this.$route, "params.playlistId", null);
      if (!playlistId || playlistId === "undefined") {
        this.loading = false;
        this.showError = true;
        this.errorMessage = "Couldn't find the playlist Id, please try later!";
        setTimeout(() => {
          this.showError = false;
        }, 5000);
        return;
      }
      if (!!this.isThereAJobForTheSamePlaylist()) {
        this.loading = false;
        this.showError = true;
        this.errorMessage = "There's already a job for this playlist!";
        setTimeout(() => {
          this.showError = false;
        }, 5000);
        return;
      }
      const url = "/jobs";
      const data = {
        job_name: "extract_keywords",
        playlist_uid: playlistId
      };

      try {
        const result = await fetch(url, {
          method: "POST",
          body: JSON.stringify(data),
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage["apollo-token"]}`
          }
        });

        const jsonResult = await result.json();
        console.log("jsonResult", jsonResult);

        const jobUid = _get(jsonResult, "job_uid", null);
        console.log("jobUid", jobUid);
        if (!jobUid) {
          this.loading = false;
          this.showError = true;
          this.errorMessage = "Couldn't get a job id, please try later!";
          setTimeout(() => {
            this.showError = false;
          }, 5000);
          return;
        }
        const newJob = {
          jobUid,
          type: "playlistKeywords",
          entityId: playlistId,
          progress: 0,
          status: "created",
          results: [],
          date: new Date()
        };
        console.log("newJob", newJob);

        let job = localStorage.getItem(jobUid);
        console.log("job", job);
        if (!job) {
          localStorage.setItem(jobUid, JSON.stringify(newJob));
        } else {
          this.loading = false;
          this.showError = true;
          this.errorMessage = "This Job already exists!";
          setTimeout(() => {
            this.showError = false;
          }, 5000);
          return;
        }
        this.loading = false;
        this.showSucess = false;
        this.showError = false;
        this.dialog = false;
        console.log("finish");
      } catch (error) {
        this.showError = true;
        this.errorMessage = "Oops we did something wrong!";
        setTimeout(() => {
          this.showError = false;
        }, 5000);
        console.log("error creating job to get playlist keywords", error);
      }
    },
    isThereAJobForTheSamePlaylist() {
      const playlistId = _get(this.$route, "params.playlistId", null);
      const jobs = Object.keys(localStorage)
        .filter(key => key.indexOf("job") > -1)
        .map(key => JSON.parse(localStorage[key]));

      console.log("jobs", jobs);

      const existingJob = jobs.find(element => element.entityId === playlistId);

      console.log("existingJob", existingJob);
      return existingJob;
    }
  }
};
</script>